.container{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 3rem;
    background-color: rgb(21, 18, 36);
    height: 100vh;
    width: 100vw;
    max-width: 1500px;
}
.title-container{
    h1{
        color: white;
    }
}
.avatars{
    display: flex;
    gap: 2rem;

    .avatar{
        border: 0.4rem solid transparent;
        padding: 0.4rem;
        border-radius: 5rem;
        display: flex;
        justify-content: center;
        align-items: center;
       
        img{
            height: 6rem;
        }
    }  
    .selected{
        border: 0.4rem solid #4e0eff;
        transition: 0.5s ease-in-out;
    }
}

.submit-btn{
    border-bottom: 1px solid #12092c;
    border-radius: 0;
    background-color: #ae5adb;
    color: white;
    width: 300px;
    height: 30px;
    font-weight: bold;
}


